.large-text-container {
  .react-large-text {
    font-family: "Courier New", Courier, monospace;
    font-size: 18px;
    background-color: khaki;

    width: 400px;
    height: 300px;

    overflow: hidden;
    resize: vertical;
    line-height: 22px;
    font-size: 18px;

    padding: 10px 10px;
    border-radius: 10px;

    &:focus-within {
      border: solid 1px saddlebrown;
      -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
      -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
      box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    }

    .content {
      .line {
        width: 100%;
        text-align: justify;
      }
    }
  }
}

.simple-scrollable-example-container {
  height: 300px;
  width: 400px;
  overflow: hidden;
  resize: both;

  .simple-scrollable-example-content {
    background-color: plum;

    width: 100%;
    height: 100%;
  }

  ul,
  li {
    margin: 0;
    border: 0;
    padding: 0;
    list-style: none;
    white-space: nowrap;
  }
}

.simple-scrollable-example{
  height: 300px;
  width: 400px;
  display: inline-block;
  resize:both;
  overflow: hidden;

  background-color: powderblue;

}
